@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
/** Media Queries **/
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  position: fixed;
  top: 5.8rem;
  right: 0.5rem;
  width: calc(100% - 25.5rem);
  height: 90%;
  overflow: auto;
  border-top: solid 1px rgb(205, 204, 204);
  border-left: solid 1px rgb(205, 204, 204);
  transition: all 0.5s ease;
  z-index: -1;
}
@media (max-width: 768px) {
  .contenedor {
    width: calc(100% - 7.5rem);
  }
}
.contenedor.collapse {
  width: calc(100% - 7.5rem);
}

.boton-azul {
  position: absolute;
  background-color: #00ABE4;
  width: 15rem;
  height: 3.8rem;
  border: 1px solid;
  border-radius: 0.4rem;
  font-weight: 500;
  font-size: 1.7rem;
  color: white;
  cursor: pointer;
  line-height: 3.8rem;
  text-align: center;
  top: 1.4rem;
  left: 1.4rem;
}
.boton-azul:hover {
  border-color: #00ABE4;
  transition: 0.4s ease;
  background-color: #00befe;
}
@media (max-width: 480px) {
  .boton-azul {
    position: relative;
    display: block;
    margin: 0 auto;
  }
}

.boton-blanco {
  background-color: whitesmoke;
  width: auto;
  padding: 0.5rem 1rem;
  border: 0.2rem solid #00ABE4;
  border-radius: 0.4rem;
  font-weight: 500;
  font-size: 1.5rem;
  color: black;
  cursor: pointer;
  text-align: center;
  display: block;
}
.boton-blanco:hover {
  border-color: white;
  border-width: 0.2rem;
  transition: 0.4s ease;
  background-color: white;
}

.boton-amarillo {
  background-color: whitesmoke;
  width: auto;
  padding: 0.5rem 1rem;
  border: 0.2rem solid #00ABE4;
  border-radius: 0.4rem;
  font-weight: 500;
  font-size: 1.2rem;
  color: black;
  cursor: pointer;
  text-align: center;
}
.boton-amarillo:hover {
  border-color: white;
  border-width: 0.2rem;
  transition: 0.4s ease;
  background-color: white;
}

.login {
  background: linear-gradient(120deg, rgba(3, 163, 255, 0.7058823529), rgba(120, 83, 169, 0.91));
  height: 100vh;
  background-size: cover;
}
.login .formulario {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 35rem;
  background-color: white;
  border-radius: 0.4rem;
}
.login .formulario h1 {
  text-align: center;
  border-bottom: solid 1px silver;
}
.login .formulario form {
  padding: 0 4rem;
}
.login .formulario form .username {
  position: relative;
  border-bottom: solid 2px silver;
  margin: 3rem 0;
}
.login .formulario form .username input {
  width: 100%;
  padding: 0 0.5rem;
  height: 4rem;
  border: none;
  background: none;
  outline: none;
}
.login .formulario form .username label {
  position: absolute;
  top: 50%;
  left: 0.5rem;
  color: rgb(205, 204, 204);
  transform: translateY(-50%);
  pointer-events: none;
  transition: 0.3s;
}
.login .formulario form .username input:focus ~ label,
.login .formulario form .username input:valid ~ label {
  top: -5px;
  color: #00ABE4;
  font-size: 1.3rem;
}
.login .formulario form .remember {
  margin: -5px 0 20px 5px;
  color: blue;
  cursor: pointer;
}
.login .formulario form .remember:hover {
  text-decoration: underline;
}
.login .formulario form input[type=submit] {
  width: 100%;
  height: 5rem;
  border: 1px solid;
  background-color: #00ABE4;
  border-radius: 0.4rem;
  font-weight: bold;
  font-size: 2rem;
  color: white;
  cursor: pointer;
  outline: none;
}
.login .formulario form input[type=submit]:hover {
  border-color: purple;
  transition: 0.4s ease;
  background-color: #00befe;
}
.login .formulario form .checkin {
  margin: 3rem 0;
  text-align: center;
  color: black;
}
.login .formulario form .checkin a {
  color: blue;
  text-decoration: none;
}
.login .formulario form .checkin a:hover {
  text-decoration: underline;
}

.sbar {
  width: 22rem;
  height: 100%;
  padding: 0 1.2rem;
  position: fixed;
  transition: all 0.5s ease;
  top: 0;
  border-right: solid 0.1rem rgb(205, 204, 204);
}
@media (max-width: 768px) {
  .sbar {
    width: 4rem;
  }
}

.sbar_head {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  border-bottom: solid 1px groove rgb(205, 204, 204);
  height: 3rem;
}
.sbar_head .sbar_head_text {
  text-transform: uppercase;
  font-size: 1.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
}
@media (max-width: 768px) {
  .sbar_head .sbar_head_text {
    display: none;
  }
}
.sbar_head .sbar_head_flech {
  position: absolute;
  width: 2.5rem;
  right: 0;
  cursor: pointer;
  transition: all 0.8s ease;
}
@media (max-width: 768px) {
  .sbar_head .sbar_head_flech {
    display: none;
  }
}

.sbar_main {
  text-decoration: none;
}
.sbar_main .sbar_main_list {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  padding: 1rem 0;
  border-bottom: solid 1px groove rgb(205, 204, 204);
}
.sbar_main .sbar_main_list a {
  text-decoration: none;
}

.sbar_main_list_item {
  display: flex;
  position: relative;
  align-items: center;
  padding: 0.5rem;
  cursor: pointer;
  border-radius: 0.3rem;
  height: 3rem;
}
.sbar_main_list_item:hover {
  background-color: #E9F1FA;
}
.sbar_main_list_item .sbar_main_list_item_icon {
  width: 2.5rem;
  justify-content: center;
}
.sbar_main_list_item .sbar_main_list_item_text {
  text-transform: uppercase;
  padding-left: 1rem;
  font-size: 1.3rem;
  transition: all 0.5s ease;
  color: black;
}
@media (max-width: 768px) {
  .sbar_main_list_item .sbar_main_list_item_text {
    display: none;
  }
}
.sbar_main_list_item .sbar_main_list_item_tip {
  position: absolute;
  top: -2.1rem;
  left: 6.4rem;
  background-color: #00ABE4;
  color: white;
  font-weight: bold;
  border: solid 2px white;
  border-radius: 0.5rem;
  padding: 0.5rem 0.9rem;
  font-size: 1.4rem;
  transition: 0s;
  opacity: 0;
  display: none;
  box-shadow: 0 0 5px 1px rgb(254, 254, 254);
}

.sbar.collapse {
  width: 4rem;
}

.sbar.collapse .sbar_head_text,
.sbar.collapse .sbar_main_list_item_text {
  opacity: 0;
  font-size: 0;
}

.sbar.collapse .sbar_main_list_item:hover .sbar_main_list_item_tip {
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.5s ease;
  opacity: 1;
  text-transform: uppercase;
}

.sbar.collapse .sbar_main_list_item_tip {
  display: block;
  width: max-content;
  pointer-events: none;
}

.sbar.collapse .sbar_head_flech {
  width: 3rem;
  transform: rotate(180deg);
  right: 25%;
}

.nbar {
  box-sizing: border-box;
  position: fixed;
  right: 0.5rem;
  height: 5rem;
  width: calc(100% - 25.5rem);
  transition: all 0.5s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  background-color: #00ABE4;
  line-height: 2.4rem;
}
.nbar span {
  color: white;
  font-weight: 550;
  font-size: 1.8rem;
}
@media (max-width: 768px) {
  .nbar {
    width: calc(100% - 7.5rem);
    font-size: 1.7rem;
    line-height: normal;
  }
}
@media (max-width: 480px) {
  .nbar {
    line-height: 1.6rem;
  }
}
.nbar .nbar_user {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 1rem;
}

.nbar.collapse {
  width: calc(100% - 7.5rem);
}

.layer-entity {
