.dialog_form {
  margin: auto;
  padding: 1rem;
  border: none;
  background-color: white;
  border-radius: 0.5rem;
  position: relative;
  box-shadow: 0 0 0.8rem 0.3rem rgba(252, 252, 252, 0.589);
}
.dialog_form .boton-cerrar {
  position: absolute;
  right: 0.5rem;
  top: 0.5rem;
  width: 3rem;
  background-color: rgb(255, 0, 0);
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  border-radius: 50%;
  cursor: pointer;
  color: white;
}
.dialog_form select {
  padding: 1rem;
  font-size: 1.5rem;
}
.dialog_form p {
  text-align: center;
  font-weight: 500;
  font-size: 1.8rem;
  text-transform: uppercase;
  padding: 2rem 0;
}
.dialog_form .dialog_form_input {
  position: relative;
  border-bottom: solid 2px silver;
  margin: 1.5rem 0;
  width: 35rem;
}
.dialog_form .dialog_form_input input {
  width: 100%;
  padding: 0 0.5rem;
  height: 4rem;
  border: none;
  background: none;
  outline: none;
}
.dialog_form .dialog_form_input label {
  position: absolute;
  top: 50%;
  left: 0.5rem;
  color: gray;
  transform: translateY(-50%);
  pointer-events: none;
  transition: 0.3s;
}
.dialog_form .dialog_form_input input:focus ~ label,
.dialog_form .dialog_form_input input:valid ~ label {
  top: -5px;
  color: #00ABE4;
  font-size: 1.3rem;
}
.dialog_form input[type=submit] {
  width: 35%;
  height: 5rem;
  border: 1px solid;
  background-color: #00ABE4;
  border-radius: 0.4rem;
  font-weight: bold;
  font-size: 2rem;
  color: white;
  cursor: pointer;
  outline: none;
}
.dialog_form input[type=submit]:hover {
  border-color: purple;
  transition: 0.4s ease;
  background-color: #00befe;
}
.dialog_form::backdrop {
  background-color: rgba(0, 0, 0, 0.711);
}
.dialog_form[open]::backdrop {
  animation: backdrop-down 0.5s forwards;
}
.dialog_form.cerrar::backdrop {
  animation: backdrop-up 0.5s forwards;
}
.dialog_form[open] {
  animation: modal-down 0.5s forwards;
}
.dialog_form.cerrar {
  animation: modal-up 0.5s forwards;
}

@keyframes backdrop-down {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes backdrop-up {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes modal-down {
  0% {
    transform: translateY(-20%);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes modal-up {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-20%);
    opacity: 0;
  }
}
